@charset "UTF-8";
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  margin: 0; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style-position: inside; }

li {
  margin: 0; }

p {
  margin: 0;
  padding: 0; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

/* Egen CSS */
/* Andet CSS */
.wrapper {
  max-width: 1300px;
  padding: 0 1em;
  margin: 0 auto; }

h1, h2, h3, h4, h5, h6, p, a, span, label {
  font-family: "Open Sans";
  color: #141C17; }

a {
  color: inherit;
  text-decoration: none; }

/* Navbar */
.navbar {
  padding: 2em 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff; }

.logo img {
  height: 40px; }

.search input {
  padding: 3px 0px;
  border: none;
  border-bottom: 1.5px solid #F2771B;
  margin-top: 0px;
  width: 300px;
  transition: 300ms ease-in-out; }

.search input:focus {
  outline: none;
  transition: 200ms ease-in-out;
  padding: 5px 3px; }

.search-icon, .nav-menu {
  display: none; }

.nav-links {
  list-style: none;
  display: flex; }

.nav-link {
  margin: 0 30px; }

.nav-item {
  text-decoration: none;
  font-weight: 600; }

.nav-icons {
  width: auto; }

.cart-icon svg {
  position: absolute; }

.cart-label {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  top: -8px;
  left: 10px;
  z-index: 1;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  font-weight: 700;
  width: 18px;
  height: 18px;
  border-radius: 100%;
  color: white;
  background: #F2771B; }

/* Katalog */
.subheader {
  padding: 6em 0;
  text-align: center; }

.katalog-grid {
  display: grid;
  grid-template-columns: 1fr 4fr; }

.filters {
  display: flex;
  flex-direction: column; }

.produkter-grid {
  display: grid;
  grid-template-columns: repeat(3, 300px);
  grid-gap: 30px;
  margin-bottom: 100px; }

.produkt-card {
  text-align: center; }

.produkt-card:hover .produkt-img > img {
  transform: scale(1.05);
  transition: 200ms ease-in-out; }

.produkt-img {
  max-width: 300px;
  max-height: 300px;
  overflow: hidden;
  margin-bottom: 20px; }

.produkt-img img {
  object-fit: cover;
  transition: 300ms ease-in-out; }

.produkt-card .label {
  padding: 5px 15px;
  position: relative;
  width: fit-content;
  top: -320px;
  font-size: 12px;
  text-transform: uppercase;
  font-family: "open sans", sans-serif;
  font-weight: 700; }

.label.udsolgt {
  background: #E03809;
  color: #ffffff; }

.label.nyhed {
  background: #93C088;
  color: #ffffff; }

.label.tilbud {
  background: #F9D401;
  color: #141C17; }

.tilbud-pris {
  opacity: 0.5;
  text-decoration: line-through;
  margin-right: 10px; }

.pagination {
  grid-column: 2;
  margin: 50px 0;
  display: flex;
  align-items: center;
  justify-content: center; }

.pagination svg {
  stroke: #F2771B; }

.pagination span {
  width: 80px;
  display: flex;
  justify-content: space-around; }

.filters p {
  margin: 5px 0; }

[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px; }

[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: #666; }

[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border: 1px solid #ececec;
  border-radius: 100%;
  background: #ececec; }

[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
  content: '';
  width: 12px;
  height: 12px;
  background: #F2771B;
  position: absolute;
  top: 4px;
  left: 4px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0); }

[type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1); }

/* Produkt side */
